import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';


class Cell extends React.Component {
  render() {
    return (
      <th style={{backgroundColor: 'white'}} onMouseEnter={this.in.bind(this)} onMouseLeave={this.out.bind(this)}>X</th>
    );
  }

  in (e) {
    e.target.style.backgroundColor='lightblue'
  }
  out (e) {
    e.target.style.backgroundColor='white'
  }
}

class Row extends React.Component {
  render() {
    return(
      <tr>
        <Cell id={this.props.id+'a'}/>
        <Cell id={this.props.id+'b'}/>
        <Cell id={this.props.id+'c'}/>
      </tr>
    );
  }
}

class Box extends React.Component {
  constructor() {
    super();
    this.state={

    };
  }
  render() {
    return(
      <table>
        <Row id='1'/>
        <Row id='2'/>
        <Row id='3'/>
      </table>
    );
  }
}


ReactDOM.render(
  <Box />,
  document.getElementById('root')
);


